import { useState, useEffect } from 'react';
import { useAuth } from '@/context/AuthContext';
import { useRouter } from 'next/navigation';
import { collection, getDocs, doc, writeBatch, query, orderBy, addDoc, limit, onSnapshot, where, Timestamp } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { motion, AnimatePresence } from 'framer-motion';
import {
    Search, Shield, Zap, Coins, Save, X, Database,
    CheckCircle, AlertCircle, History, Users,
    Trophy, Swords, MapPin, Camera, ShoppingBag,
    Trash2, UserPlus, FileText, Activity
} from 'lucide-react';

// --- DATA TYPES ---
interface UserData {
    id: string;
    displayName: string;
    email: string;
    // Use string for flexible searching (inputs are usually text)
    phone?: string;
    codename?: string;
    rank: string;
    exp: number;
    coins: number;
    partyId: string | null;
    photoURL?: string;
}

interface LogEntry {
    id: string;
    action: string;
    targets: string[]; // Names of people affected
    details: string;
    adminEmail: string;
    timestamp: any;
    type: 'exp' | 'admin' | 'party';
}

// --- CONFIGURATION (Synced with App Script) ---
